<template>
  <div>
    <div class="row mb-4">
      <div class="col-md-11 col-sm-6">
        <h4 class="fw-bold">Create Plan</h4>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/plans">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <template>
      <div class="card">
        <div class="card-body">
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-card-title class="h6 p-0">GENERAL</v-card-title>

            <v-text-field label="Name (required)" v-model="plandata.plan_description" :rules="nameRules" required></v-text-field>

            <v-text-field type="number" label="Plan Expiry (In days)" v-model="plandata.plan_expiry" :rules="expiryRules" required></v-text-field>

            <v-text-field type="number" label="Price (required)" v-model="plandata.plan_price" :rules="priceRules" required></v-text-field>

            <v-card-title class="h6 p-0">LIMITATIONS</v-card-title>

            <v-text-field type="number" label="Images (required)" v-model="plandata.images" :rules="imagesRules" required></v-text-field>

            <v-row class="mb-2">
              <v-col cols="12" sm="4" md="3">
                <v-text-field type="number" label="Hot Deals" v-model="plandata.hot_deals" required></v-text-field>
                <v-checkbox v-model="plandata.reviews" label="Reviews" hide-details></v-checkbox>
                <v-checkbox v-model="plandata.services" label="Services" hide-details></v-checkbox>
<!--                <v-checkbox v-model="plandata.hot_deals" label="Hot Deals" hide-details></v-checkbox>-->
<!--                <v-checkbox v-model="plandata.video" label="Video" hide-details></v-checkbox>-->
<!--                <v-checkbox v-model="plandata.sale" label="Sale" hide-details></v-checkbox>-->
<!--                <v-checkbox v-model="plandata.jobs" label="Jobs" hide-details></v-checkbox>-->

              </v-col>
              <v-col cols="12" sm="4" md="3">
                <v-text-field type="number" label="Video" v-model="plandata.video" required></v-text-field>
<!--                <v-text-field type="number" label="Reviews" v-model="plandata.reviews" required></v-text-field>-->
<!--                <v-text-field type="number" label="About" v-model="plandata.about" required></v-text-field>-->
<!--                <v-text-field type="number" label="Services" v-model="plandata.services" required></v-text-field>-->
<!--                <v-text-field type="number" label="Contact" v-model="plandata.contact" required></v-text-field>-->
                <v-checkbox v-model="plandata.contact" label="Contact" hide-details></v-checkbox>
                <v-checkbox v-model="plandata.about" label="About" hide-details></v-checkbox>
              </v-col>
              <v-col cols="12" sm="4" md="3">
                <v-text-field type="number" label="Sale" v-model="plandata.sale" required></v-text-field>
<!--                <v-text-field type="number" label="Featured City" v-model="plandata.featured_city" required></v-text-field>-->
<!--                <v-text-field type="number" label="Featured Category" v-model="plandata.featured_category" required></v-text-field>-->
<!--                <v-text-field type="number" label="Home Page Banner" v-model="plandata.home_page_banner" required></v-text-field>-->
                <v-checkbox v-model="plandata.featured_city" label="Featured City" hide-details></v-checkbox>
                <v-checkbox v-model="plandata.featured_category" label="Featured Category" hide-details></v-checkbox>
              </v-col>
              <v-col cols="12" sm="4" md="3">
                <v-text-field type="number" label="Jobs" v-model="plandata.jobs" required></v-text-field>
<!--                <v-text-field type="number" label="Featured City" v-model="plandata.featured_city" required></v-text-field>-->
<!--                <v-text-field type="number" label="Featured Category" v-model="plandata.featured_category" required></v-text-field>-->
<!--                <v-text-field type="number" label="Home Page Banner" v-model="plandata.home_page_banner" required></v-text-field>-->
                <v-checkbox v-model="plandata.home_page_banner" label="Home Page Banner" hide-details></v-checkbox>
              </v-col>
            </v-row>

            <v-btn @click="addplan" :disabled="!valid" color="success" class="mr-4" >
              SAVE
            </v-btn>
              <v-btn color="error" class="mr-4" @click="reset">
                CANCEL
              </v-btn>
          </v-form>
          <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
            <template v-slot:action="{attrs}">
              <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
            </template>
          </v-snackbar>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CrateNewplan",
  components: {  },
  data(){
    return{
      plandata:{
        plan_description: '',
        plan_expiry: '',
        plan_price: '',
        plan_status: '1',
        images: '',
        hot_deals: '',
        about: '',
        video: '',
        services: '',
        contact: '',
        reviews: '',
        sale: '',
        jobs: '',
        featured_city: '',
        featured_category: '',
        home_page_banner: '',
      },
      valid: true,
      snackbar: false,
      text: '',
      timeout: -1,
      nameRules: [
        v => !!v || 'The name field is required.',
      ],
      expiryRules: [
        v => !!v || 'The plan expiry field is required.',
      ],
      priceRules: [
        v => !!v || 'The Price field is required.',
      ],
      imagesRules: [
        v => !!v || 'The Images field is required.',
      ],
    }
  },
  methods: {
    reset(){
      this.$refs.form.reset();
    },
    addplan() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        plan_status: 1,
      };
      axios.post("admin/add/plan",this.plandata,data,config)
          .then((resp) => {
            if(resp.data.message) {
              this.snackbar = true;
              this.text = resp.data.message;
              this.timeout = 3000;
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            // Handle the error
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
    },
  },
}
</script>